body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fa-shopping-cart {
  font-size: 22px;
  position: relative;
}

.counter {
  position: absolute;
  top: -6px;
  left: 16px;
  font-size: 11px;
  border-radius: 10px;
}

#showcase {
  /* background: url("./images/bg-header.png") no-repeat; */
  background: url("./images/showcase.jpg") no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 750px;
  position: relative;
  overflow: hidden;
}
#showcase .showcase-overlay {
  position: absolute;
  background: #2b2e4a;
  opacity: 0.9;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#showcase .showcase-content {
  padding-top: 230px;
  color: #fff;
  width: 70%;
  margin: auto;
}

.showcase-content h1 {
  font-size: 1.5rem;
  text-align: center;
}

@media (min-width: 768px) {
  .showcase-content h1 {
    font-size: 3rem;
  }
}

@media (min-width: 992px) {
  .showcase-content h1 {
    font-size: 3.5rem;
  }
}

#section-1 h2 {
  font-size: 18px;
  color: black;
}

#section-1 .fa {
  font-size: 50px;
  margin-right: 10px;
  color: blue;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px; /* 5px rounded corners */
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.card button,
.add-to-card {
  border: none;
  outline: 0;
  padding: 12px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}

.card button:hover,
.add-to-card:hover {
  opacity: 0.7;
}

.add-to-card {
  width: 160px;
}

#footer-section {
  background: #3a3e64;
}
#footer-section h6 {
  color: #fff;
  font-weight: bold;
}
#footer-section .upper-footer {
  padding-top: 60px;
  padding-bottom: 60px;
}
#footer-section .fa {
  font-size: 25px;
  border: 1px solid #fff;
  padding: 10px 15px;
  color: #fff;
}

.below-footer {
  background: #313454;
}

.list-group-item {
  cursor: pointer;
}

.slick-prev:before {
  color: #000;
}

.slick-next:before {
  color: #000;
}

.contact-bg-image {
  /* background: url("./images/bg-01.jpg") no-repeat; */
  background: url("./images//contact-bg.png") no-repeat;
  /* height: 839px; */
  /* width: 560px; */
  /* width: 100%; */
  height: 400px;
  color: white;
  /* position: relative; */
}

.overlay {
  /* position: absolute; */
  margin-top: 30px;
  margin-left: -10px;
  padding: 20px;
  /* width: 100%; */
  /* height: 100%; */
  /* background: rgba(0, 0, 0, 0.4); */
}

.card-columns {
  column-count: 1;
}

@media (min-width: 768px) {
  .card-columns {
    column-count: 2;
  }
}

@media (min-width: 992px) {
  .card-columns {
    column-count: 3;
  }
}

.tab-content {
  padding: 50px 15px;
  border: 1px solid #ddd;
  border-top: 0;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

.single {
  padding: 0px 15px 30px;
  /* margin-top: 40px; */
  background: #fcfcfc;
  border: 1px solid #f0f0f0;
}
.single h3.side-title {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  font-size: 20px;
  color: #333;
  text-transform: uppercase;
}
.single h3.side-title:after {
  content: "";
  width: 60px;
  height: 1px;
  background: #ff173c;
  display: block;
  margin-top: 6px;
}

.single ul {
  margin-bottom: 0;
}
.single li {
  color: #666;
  font-size: 14px;
  text-transform: uppercase;
  border-bottom: 1px solid #f0f0f0;
  line-height: 40px;
  display: block;
  text-decoration: none;
  cursor: pointer;
}

.single li:hover {
  color: #ff173c;
}

.category-active {
  color: #ff173c !important;
}

.single li:last-child a {
  border-bottom: 0;
}
